import React, { Component } from "react";
import Instafeed from "instafeed.js";

class InstaComponent extends Component {
  state = {};
  render() {
    window.addEventListener("load", function () {
      var feed = new Instafeed({
        limit: 10,
        sortBy: "most-liked",
        resolution: "standard_resolution",
        accessToken: "rwh10fw07t8ax2s8ck0tl",
        template:
          '<div class="tile"><div class="text"><b>{{likes}} &hearts; </b>{{model.user.full_name}}</div><img class="item" src="{{image}}"></div>',
        filter: function (image) {
          return image.type === "video";
        },
      });
      feed.run();
    });

    return (
      <React.Fragment>
        <h1>Mah Music</h1>
        <div id="instafeed"></div>
      </React.Fragment>
    );
  }
}

export default InstaComponent;
